.modal.modal-our {
  @include mobile {
    width: 100%;
  }

  @media screen and (max-width: 768px) and (min-width: 480px){
    height: 449px;
  }

  @include tablet {
    width: 568px;
    height: 449px;
  }
}

.modal__map-btn {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  min-width: 145px;
  display: inline-block;
  padding: 12px 24px;
  margin-top: 32px;
  background: $bg-color-button;
  border-radius: 10px;
  border: none;
  align-items: center;
  text-align: center;
  text-decoration: none;
  color: $primary-text-color;

  @media screen and (max-width: 480px) {
      
    margin-top: 24px;
  }
}

.modal__map {
  width: 100%;
  height: 205px;
  margin-bottom: 32px;
  border-radius: 10px;

  @media screen and (max-width: 480px) {
    height: 199px;
    margin-bottom: 0;
 }
}



  